  width: 36px;
  height: 36px;
  border: 1px solid #E5DDD5;
  background-color: #fff;
  position: absolute;
  top: -10px;
  left: -60px;
}
.checkbox__decor::after {
  content: "";
  width: 8px;
  height: 20px;
  position: absolute;
  top: 5px;
  left: 15px;
  transform: rotate(45deg);
}
.appointment__form input:checked ~ .checkbox__decor::after {
  border: 2px solid #000;
  background-color: #fff;
  border-top: none;
  border-left: none;
}
